<template>
  <div id="jsContainer">
    <ul class="js-test" id="jsParent">
        <li data-index="0">1</li>
        <li data-index="1">2</li>
    </ul>
    <span style="font-weight: bold;">3</span>
    4

    <p id ='res'></p>
</div>
</template>

<script>
import Intro from './Intro'
import About from './About'
import Education from './Education'
import Projects from './Projects'
import Contact from './Contact'
import Internship from './Internship'
export default {
  components: {Internship, Contact, Projects, Education, About, Intro},
  data () {
    return {
      activeIndex: '1',
      arrDom: [],
      headerFixed: false
    }
  },
  mounted () {
    this.dom2json();
    // this.arrDom = document.getElementsByClassName('item-content')
    // window.addEventListener('scroll', this.handleScroll)
  },

  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      for (let i = 0; i < this.arrDom.length; i++) {
        if (this.arrDom[this.arrDom.length - 1].offsetTop - scrollTop > 760) {
          if (this.arrDom[i].offsetTop - scrollTop <= 760 && this.arrDom[i + 1].offsetTop - scrollTop > 760) {
            this.activeIndex = (i + 1) + ''
          }
        } else {
          this.activeIndex = this.arrDom.length + ''
        }
      }
    },
    handleSelect (key, keyPath) {
      this.activeIndex = key
      let pageId = document.querySelector('#page' + key)
      let nav = document.getElementById('nav-container')
      window.scrollTo({
        'top': pageId.offsetTop - nav.offsetHeight,
        'behavior': 'smooth'
      })
    },
    dom2json() {
      console.log(JSON.stringify(this.elementToObject(undefined)))

      },
     elementToObject(element) {
        var el;

        if (!element){
          el = document.getElementById('jsContainer')
        }
        else el = element;
        var o = {
          tag: el.tagName,
        };
        if (el.attributes){
          o.attributes = {}

        var i = 0;
        for (i ; i < el.attributes.length; i++) {
          o['attributes'][el.attributes[i].name] = el.attributes[i].value;
        }}

        var children = el.childNodes;
        if (children.length) {
          o.children = [];
          i = 0;
          for (i; i < children.length; i++) {
            let child = children[i];
            o.children[i] = this.elementToObject(child, o.children);
          }
        }
        return o;
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-menu{
  float: right;
  }
  #nav-container{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: white;
    z-index: 5;
  }
</style>



